@import url(https://fonts.googleapis.com/css2?family=Monoton&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #14171a;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.heading {
  font-family: 'Monoton', cursive;
  font-size: 50px;
  font-weight: 100;
  color: #fff;
}

input {
  padding: 18px;
  font-size: 25px;
  outline: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.3);
  border: none;
  color: #fff;
}

input::-webkit-input-placeholder {
  color: #fff;
}

input:-ms-input-placeholder {
  color: #fff;
}

input::-ms-input-placeholder {
  color: #fff;
}

input::placeholder {
  color: #fff;
}

input:focus {
  box-shadow: 2px 2px 10px rgba(255, 255, 255, 0.1), -2px -2px 10px rgba(255, 255, 255, 0.1);
}
